import React from 'react';
import { push } from 'react-router-redux'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import {Link} from 'react-router-dom';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Button } from 'reactstrap';
  import {
    login,
    loginWithAPI,
    logout
  } from '../../modules/user'
class Header extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    },
  ()=>{
    console.log(this.state);
    this.forceUpdate();
  });
  }
  getLink(text,link){
    return  (
      <Link className="dropdown-item" to={link}>{text}</Link>
    )
  }
  getNav(){
    return (
      <Link className="navbar-brand" to="/">LessForDress</Link>
    )
  }
  render() {
    return (
      <div>
      <div className="d-none d-sm-block">
        <Navbar dark={true} color="dark" expand="xs">
          <NavbarBrand tag={this.getNav}></NavbarBrand>
            <Nav className=" ml-auto" navbar>
              <NavItem>
                <Link className="nav-link" to="/about-us">About</Link>
              </NavItem>
              <NavItem>
                <Link className="nav-link" to="/information">My Dashboard</Link>
              </NavItem>
              {!this.props.loggedIn && (<NavItem>
                <Link className="nav-link" to="/login">Login</Link>
              </NavItem>)}
              {this.props.loggedIn && (<UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Options
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem tag={()=>this.getLink("My Cart","/cart")}>

                  </DropdownItem>
                  <DropdownItem tag={()=>this.getLink("My Profile","/profile")}>

                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem tag={()=>(<button className="dropdown-item"  onClick={this.props.logout}>Logout</button>)}>

                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>)}
            </Nav>
        </Navbar>
      </div>
      <div className="d-block d-sm-none">
        <Navbar dark={true} color="dark" expand="xs">
          <NavbarBrand tag={this.getNav}></NavbarBrand>
            <Nav className=" ml-auto" navbar>
              <NavItem>
                <Link className="nav-link" to="/information"><i className="fa fa-window-maximize"></i></Link>
              </NavItem>
              {!this.props.loggedIn && (<NavItem>
                <Link className="nav-link" to="/login"><i className="fa fa-sign-in"></i></Link>
              </NavItem>)}
              {this.props.loggedIn && (<UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  <i class="fa fa-user-o"></i>
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem tag={()=>this.getLink("My Cart","/cart")}>

                  </DropdownItem>
                  <DropdownItem tag={()=>this.getLink("My Profile","/profile")}>

                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem tag={()=>(<button className="dropdown-item" onClick={this.props.logout}>Logout</button>)}>
                    Reset
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>)}
            </Nav>
        </Navbar>
      </div>

      </div>
    );
  }
}
const mapStateToProps = state => ({
  loggedIn: state.user.loggedIn,
  loggingIn: state.user.loggingIn,
})
const mapDispatchToProps = dispatch => bindActionCreators({
  login,
  loginWithAPI,
  logout
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Header)



